<script>
import { baseModalMixins, formModalMixins } from "@/mixins/modalMixin";
import { setGateway as fetchAPI } from "@/api/network/router";

export default {
  mixins: [baseModalMixins, formModalMixins],
  data() {
    return {
      fetchAPI,
      name: "gateway",
      externalId: '',
